import React from 'react';
import {render} from 'react-dom';
import {Row,Col,Form, FormControl,ControlLabel,FormGroup,Image,Panel} from 'react-bootstrap';

export default class Register extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            'username':'',
            'password':'',
            'caption' :'1234'
        }
    }
    
    render(){
        return(
            <Panel>
                <Row className="show-grid">
                    <Col xs={12} sm={8} smOffset={3}>
                        <Form>
                            <FormGroup>
                                <Col componentClass={ControlLabel} sm={2}>
                                    
                                </Col>
                                <Col sm={10}>
                                    <FormControl type="text" placeholder=""  size="30"/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Col componentClass={ControlLabel} sm={2}>
                                    
                                </Col>
                                <Col sm={10}>
                                    <FormControl type="password" placeholder=""  size="30"/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Col componentClass={ControlLabel} sm={2}>
                                    
                                </Col>
                                <Col sm={10}>
                                    <FormControl type="password" placeholder=""  size="30"/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Col componentClass={ControlLabel} sm={2}>
                                    
                                </Col>
                                <Col sm={10}>
                                    <FormControl type="password" placeholder=""  size="30"/>
                                </Col>
                            </FormGroup>
                            <FormGroup>
                                <Col componentClass={ControlLabel} sm={2}>
                                    
                                </Col>
                                <Col sm={3}>
                                    <FormControl type="text" placeholder="" size="15"/>
                                </Col>
                                <Col sm={2}>
                                    <Image src="" alt="photo"/>
                                </Col>
                            </FormGroup>
                            <button type="submit" className="btn btn-primary btn-md rui-center"></button>
                        </Form>
                    </Col>
                </Row>
            </Panel>
        )
    }
}


